<template>

    <van-pull-refresh
            ref="pullrefresh"
            v-model="state.loading"
            success-text="刷新成功"
            @refresh="onRefresh"
            style="overflow-y: scroll;position: relative;height: 93vh">
        <news-list-item
                v-for="(item,index) in state.datas"
                :onPress="onPress"
                :item='item'
                :key="index"
        />


    </van-pull-refresh>

</template>

<script>
  import {reactive, onMounted, watch, ref} from 'vue'
  import {getNewsList} from '../../service/health-consultation/index'
  import NewsListItem from './NewsListItem'

  export default {
    name: "index",
    props: {
      typeId: {
        type: String,
        default: ''
      },
      item: {
        type: Object,
        default: null
      },
      onPress: {
        type: Function,
        default: () => {
        }
      },
    },
    components: {NewsListItem},
    setup(props) {
      let pullrefresh = ref(null);
      onMounted(
        () => {
          getDatas()

          // console.dir(pullrefresh.value)
          //
          // pullrefresh.value.on("scroll", () => {
          //   if(pullrefresh.value.clientHeight + pullrefresh.value.scrollTop >= pullrefresh.value.scrollHeight){
          //     state.page = ++state.page
          //   }
          // })
        }
      )
      // 通过reactive 可以初始化一个可响应的数据，与Vue2.0中的Vue.observer很相似
      const state = reactive({
        todo: '',
        page: 1,
        datas: []
      })

      // 监听 reactive 中的单个属性
      watch(
        () => state.page,
        (count, prevCount) => {
          console.log(count, prevCount)
          getDatas()
        }
      )


      const getDatas = async () => {
        console.log('state.page===', state.page)
        console.log('props.typeId===', props.typeId)
        const res = await getNewsList(state.page, props.typeId + "")
        const _data = res.data.records
        for (let i = 0; i < _data.length; i++) {
          if (_data[i].newMode == 3) {
            _data[i].imagetbUrl1 = _data[i].imagetbUrl1.replace('http://static.youjiankang.net', 'https://www.youjiankang.net')
            _data[i].imagetbUrl2 = _data[i].imagetbUrl2.replace('http://static.youjiankang.net', 'https://www.youjiankang.net')
            _data[i].imagetbUrl3 = _data[i].imagetbUrl3.replace('http://static.youjiankang.net', 'https://www.youjiankang.net')

          } else {
            _data[i].imageUrl = _data[i].imageUrl.replace('http://static.youjiankang.net', 'https://www.youjiankang.net')

          }
        }

        console.log('获取资讯分页数据', res);
        state.datas = [...state.datas, ...res.data.records]
      }


      const onRefresh = () => {
        state.loading = false
        state.page = 10
      }


      // 在Vue3.0中，所有的数据和方法都通过在setup 中 return 出去，然后在template中使用
      return {
        state,
        onRefresh,
        pullrefresh,
      }
    }
  }
</script>

