<template>
    <div style="height: 100%;width: 100%">

<!--        <s-header :name="'健康资讯'"/>-->

        <van-tabs>
            <van-tab style="overflow-y: scroll;" v-for="(item, index) in state.types" :title="item.typeName" :key="index">
                <news-list
                        :onPress="newlistonPress"
                        :typeId="item.typeId"
                />
            </van-tab>

        </van-tabs>
    </div>
</template>

<script>
  import {reactive, onMounted,} from 'vue'
  import sHeader from '@/components/SimpleHeader'
  import {getBaNewsTypeList} from '../../service/health-consultation/index'
  import newsList from './NewsList'
  import {useRouter} from 'vue-router'

  export default {
    name: "index",
    components: {
      newsList
    },
    setup() {
      window.top.postMessage(JSON.stringify({ msg:"我是资讯列表",page:"NewsList"}),"*")
      console.log( "window.top.postMessage", window.top.postMessage)
      const router = useRouter()
      document.title = '健康资讯'
        onMounted(
        async () => {

          const res = await getBaNewsTypeList()
          state.types = res.data
          console.log('获取类型列表数据', res);
        }
      )

      // 通过reactive 可以初始化一个可响应的数据，与Vue2.0中的Vue.observer很相似
      const state = reactive({
        todo: '',
        isLoading: false,
        types: []

      })

      const onRefresh = () => {
        state.loading = false;
      }

      const newlistonPress = (item) => {
        console.log('item===', item)
        window.top.postMessage(JSON.stringify({ msg:"我是资讯详情页",page:"NewsListDetail"}),"*")
        router.push({path: '/news-list-detals', query: item ,title:'资讯详情'})
      }

      // 在Vue3.0中，所有的数据和方法都通过在setup 中 return 出去，然后在template中使用
      return {
        state,
        sHeader,
        newsList,
        onRefresh,
        newlistonPress
      }
    }
  }
</script>

<style scoped lang="less">
    .content {
        margin: 10px 15px 0 15px;
        background-color: #CEF1E9;
        height: 100%;
        position: relative;
        height: calc(100vh - 20px)
    }


</style>
